  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

ul, li a {
  text-decoration: none;
  list-style: none;
}

.centered {
  width: 100%;
  aspect-ratio: 16 / 9;
  margin: 0 auto;
  display: block;
}

#hw6-link, #hw7-link, #hw8-link {
  width: 100%;
  max-width: 300px;
  color: #fff;
